.camera {
	.wrapper {
		position: relative;
		background-image: url('../img/background.jpg');
		background-size: 100% 100%;
		background-position: top center;
		background-repeat: no-repeat;

		flex-direction: column;
		justify-content: flex-start;

	}

	.back-button {
		position: absolute;
		align-self: flex-start;
		margin-top: 62px * $ratio;
		margin-left: 65px * $ratio;
		width: 109px * $ratio;
		height: 109px * $ratio;
		border-radius: 50%;
		background: #fff;
	}

	.close-button {
		position: absolute;
		align-self: flex-start;
		margin-top: 200px * $ratio;
		margin-left: 65px * $ratio;
		width: 109px * $ratio;
		height: 109px * $ratio;
		border-radius: 50%;
		background: #fff;
	}

	.real-como-santal {
		margin-top: 90px * $ratio;
	}


	.camera-frame {
		$width: 557px;
		$height: 417px;
		position: relative;
		width: $width;
		height: $height;
		margin-top: 120px;
		background-color: #241f20;
		border-radius: 32px;

		overflow: hidden;

		.fotografia {
			width: 100%;
			height: 100%;
		}
	}

	.shutter {
		$size: 80px;

		display: block;
		position: absolute;
		left: 50%;
		bottom: 16px;
		margin-left: -($size/2);
		width: $size;
		height: $size;
		background-color: #ffffff;
		border-radius: 50%;
		cursor: pointer;
		z-index: 9999;

		i {
			display: block;
			font-size: $size / 2;
			color: $brand-color;
			text-align: center;
			line-height: $size;
		}
	}

	.reset {
		@extend .shutter;
		display: none;
	}

	.download {
		@extend .shutter;
		$size: 60px;

		display: none;
		width: $size;
		height: $size;
		margin-top: $size / 6;
		margin-bottom: $size / 6;
		margin-left: 48px;

		i {
			font-size: $size / 2;
			line-height: $size;
		}
	}

	.continue {
		display: none;
		margin-top: 32px;
		margin-left: auto;
		margin-right: auto;
		padding: 0;
		border: 2.6px * $ratio solid $brand-color;
		border-radius: 24px * $ratio * 1.5;
		width: 503px * $ratio;
		height: 92px * $ratio;
		background: $brand-color;
		font-family: 'Raleway';
		font-weight: 900;
		font-size: 42.27px * $ratio;
		line-height: 46px * $ratio;
		text-align: center;
		text-transform: uppercase;
		color: #fff;
		cursor: pointer;

		&:hover {
			background-color: $brand-color-alt;
			color: #fff;
			border-color: $brand-color-alt;
		}
	}
}